import Vue from "vue";
import "./bond-row-component";
import loadReport from "./load-report";
import IReportRow from "./report-row";
import { hasOffer, isVarCoupon } from "./report-row-extensions";
import { ISettings, load as loadSettings, save as saveSettings } from "./settings";
import storageUtil from "./storageUtil";

interface IViewModel {
    settings?: ISettings;
    reportDate?: string;
    reportRows?: ReadonlyArray<IReportRow>;
    reportError?: boolean;
    hasFits?: boolean;
    hasABI?: boolean;
    hasCBHY?: boolean;
    hasHighRisk?: boolean;
    hasQualOnly?: boolean;
    collapseForm?: boolean;
    showVarCouponNotice?: boolean;
    showOfferNotice?: boolean;
    showAlert?: boolean;
}

const LAUNCH_TIME = Date.now();
const ROOT_ID = "vue-root";
const IS_CORDOVA = "cordova" in window;
const ALERT_DISMISS_KEY = "alert-v1";

const VM: IViewModel = {
    collapseForm: window.matchMedia && window.matchMedia("(max-width: 992px)").matches
};

if (IS_CORDOVA) {
    document.addEventListener("deviceready", onCordovaReady, false);
    document.addEventListener("DOMContentLoaded", onDOMContentLoaded, false);
}

loadReport((report) => {
    VM.settings = loadSettings();
    VM.reportDate = report.date;
    VM.reportRows = report.rows;

    const reportError = report.error;
    VM.reportError = reportError;
    VM.showAlert = !reportError && !storageUtil.load(ALERT_DISMISS_KEY);

    applySettings();

    // tslint:disable-next-line:no-unused-expression
    new Vue({
        el: "#" + ROOT_ID,
        data: VM,
        watch: {
            settings: {
                deep: true,
                handler: () => applySettings()
            }
        },
        methods: {
            alertClose: () => storageUtil.save(ALERT_DISMISS_KEY, true)
        }
    });

    document.getElementById(ROOT_ID).style.display = "";
    document.getElementById("bond-finder-loading").style.display = "none";

    if (!IS_CORDOVA) {
        initFontSize();
    }
});

function applySettings() {
    saveSettings(VM.settings);

    const settings = VM.settings;
    const maturityHorizon = [ settings.year, settings.month, "01" ].join("-");
    const maxListLevel = Number(settings.maxListLevel);
    const maxPerYear = Number(settings.maxPerYear);
    const requiredCouponMonth = Number(settings.requiredCouponMonth);
    const maxAmort = Number(settings.maxAmort);
    const minTotalTradesPerc = Number(settings.minTotalTradesPerc);

    const couponType = Number(settings.couponType);
    const allowVarCoupon = couponType > 0;
    const allowOffer = couponType > 1;

    VM.hasFits = false;
    VM.hasABI = false;
    VM.hasCBHY = false;
    VM.hasHighRisk = false;
    VM.hasQualOnly = false;
    VM.showVarCouponNotice = false;
    VM.showOfferNotice = false;

    for (const row of VM.reportRows) {
        row.fits = fits(row);
        if (row.fits) {
            VM.hasFits = true;
            if (isVarCoupon(row)) {
                VM.showVarCouponNotice = true;
            }
            if (hasOffer(row)) {
                VM.showOfferNotice = true;
            }
            VM.hasABI = VM.hasABI || row.inABI;
            VM.hasCBHY = VM.hasCBHY || row.inCHBY;
            VM.hasHighRisk = VM.hasHighRisk || row.isHighRisk;
            VM.hasQualOnly = VM.hasQualOnly || row.isQualOnly;
        }
    }

    function fits(row: IReportRow) {
        if (row.isHighRisk && !settings.highRisk) {
            return false;
        }

        if (maturityHorizon.length < 10 || row.maturityDate >= maturityHorizon) {
            return false;
        }

        if (settings.sector1 || settings.sector2 || settings.sector3) {
            if (row.sector === 1 && !settings.sector1) { return false; }
            if (row.sector === 2 && !settings.sector2) { return false; }
            if (row.sector === 3 && !settings.sector3) { return false; }
        }

        if (row.listLevel > maxListLevel) {
            return false;
        }

        if (maxPerYear > -1) {
            if (row.couponMonths[0] === -12) {
                return false;
            }
            if (row.couponMonths.length > maxPerYear) {
                return false;
            }
        }

        if (requiredCouponMonth > 0) {
            if (row.couponMonths.indexOf(requiredCouponMonth) < 0) {
                return false;
            }
        }

        if (!allowVarCoupon && isVarCoupon(row)) {
            return false;
        }

        if (!allowOffer && hasOffer(row)) {
            return false;
        }

        if (row.minDaysBetweenAmort > 0) {
            if (maxAmort === -1) {
                return false;
            }
            if (row.minDaysBetweenAmort < maxAmort) {
                return false;
            }
        }

        if (row.totalTradesPerc < minTotalTradesPerc) {
            return false;
        }

        return true;
    }
}

function onCordovaReady() {
    document.addEventListener("resume", onCordovaResume, false);
    (window as any).MobileAccessibility.usePreferredTextZoom(false);
}

function onCordovaResume() {
    if (Date.now() - LAUNCH_TIME > 1234 * 1000) {
        location.reload();
    }
}

function initFontSize() {
    const html = document.documentElement;
    html.style.fontSize = "medium";
    html.style.fontSize = 1600 / parseFloat(getComputedStyle(html).fontSize) + "%";
}

function onDOMContentLoaded() {
    document.addEventListener("click", (e) => {
        const a = e.target as HTMLAnchorElement;
        if (a.tagName === "A" && !confirmExternalLink(a.href)) {
            e.preventDefault();
            e.stopPropagation();
        }
    }, false);
}

function confirmExternalLink(href: string) {
    if (!href) {
        return true;
    }

    const kind = href.indexOf("moex.com/") > -1 ? "moex" : "other";
    const storageKey = "confirm-external-link-" + kind;

    if (!storageUtil.load(storageKey)) {
        const subj = kind === "moex"
            ? "сайт Московской Биржи"
            : "внешний сайт";
        if (!confirm("Пожалуйста, подтвердите переход на " + subj)) {
            return false;
        }
        storageUtil.save(storageKey, "1");
    }

    return true;
}
